
#login .vux-header {
	border-bottom: 1px solid #000;
}
#login .vux-header-title {
	height: .96rem;
	line-height: .96rem;
	font-size: .38rem;
	color: #000;
}
#login .vux-header-left {
	left: .35rem;
	top: .35rem;
}
#login .left-arrow:before {
	width: .3rem;
	height: .3rem;
	border-color: #000;
}
#login .content {
	height: 5rem;
	background-color: #eee;
}
#login .content>div {
	padding-top: .3rem;
	margin: 0 auto;
	width: 90%;
}
#login .weui-cells {
	border: 1px solid #f2f2f2;
	border-radius: .1rem;
}
#login .vux-tab {
	position: fixed;
	bottom: 0;
	left: 0;
	border-top: 1px solid #000;
	width: 100%;
	height: 1rem;
}
#login .vux-tab-item {
	font: .3rem/1rem '微软雅黑';
}
#login .iconfont {
	font-size: .35rem;
}
#login .password {
	color: #33334c;
}
#login .weui-cells {
	font-size: .3rem;
}
#login input[type='button'] {
	margin: .3rem 0;
	border-radius: .3rem;
	border: none;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	height: 1rem;
	font: .3rem/1rem '微软雅黑';
	color: #000;
	width: 100%;
}
#login .weui-icon-clear:before {
	font-size: .3rem;
}
#login .register {
	padding: .2rem;
	font: .28rem/.6rem '微软雅黑';
}



