
#detail .nav {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    width: 100%;
    background-color: #745962;
}
#detail>div {
    padding-top: 1rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#detail article {
		padding: .2rem;
}
#detail article header {
		position: relative; 
    height: 1rem;
}
#detail article img {
		position: absolute;
		left: 0;
		top: 0;
		width: .7rem;
		height: .7rem;
}
#detail .author {
		margin-left: .9rem;
		font: .25rem/.4rem '微软雅黑';
}
#detail .author div {
    color: #4c93d4;
}
#detail .audio {
		position: absolute;
		right: .3rem;
		top: 0;
		text-align: center;
}
#detail .audio i {
		font-weight: bold;
		font-size: .5rem;
		color: #745962;
}
#detail .audio p {
		font: .2rem/.5rem '微软雅黑'
}
#detail h3 {
		font: bold .3rem/.5rem '微软雅黑';
}
#detail .mainbody div {
		font: .27rem/.4rem '微软雅黑';
}
#detail .question_title {
    font: bold .3rem/.4rem '微软雅黑';
}
#detail .question_content, 
  #detail .answer_content {
    font: .28rem/.4rem '微软雅黑';
}
#detail .user {
    padding-top: .5rem;
    font: .28rem/.4rem '微软雅黑';
    color: #4c93d4;
}



