
#user .vux-header-title {
		height: .96rem;
		line-height: .96rem;
		font-size: .38rem;
		color: #000;
}
#user .vux-header-left {
		left: .35rem;
		top: .35rem;
}
#user .left-arrow:before {
		width: .3rem;
		height: .3rem;
		border-color: #000;
}
#user .content {
		text-align: center;
		height: 5rem;
		font: .25rem/.3rem '微软雅黑';
		background-color: #eee;
}
#user .userIcon {
		padding: .3rem;
		width: 1rem;
		height: 1rem;
}
#user .userIcon img {
		border-radius: 50%;
}
#user .username {
		text-align: center;
		font: .25rem/.3rem '微软雅黑';
}
#user i {
    font-size: .5rem;
    color: #000;
}
#user .vux-header-right {
    width: 1rem;
    height: .96rem;
    text-align: center;
    top: 0;
    right: 0;
    line-height: 1rem;
}
#user .vux-header-right span {
    display: block;
    font-size: .3rem;
    color: #000;
}
#user .login {
    margin: 2rem auto;
    text-align: center;
    font: 1rem '微软雅黑';
}
	



