
#musicPages .poster {
	position: relative;
	height: 3.5rem;
	text-align: center;
}
#musicPages .poster img {
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -1rem 0 0 -1rem;
	width: 2rem;
	height: 2rem;
}
#musicPages .poster i {
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -.5rem 0 0 -.5rem;
	font-size: 1rem;
	color: #fff;
}
#musicPages .poster p {
	padding-top: 3rem;
	font: .25rem '微软雅黑';
	color: #fff;
}
#musicPages .content {
 padding: .3rem;
 font: .28rem/.34rem '微软雅黑';
}
#musicPages .content .author {
	color: #4c93d4;
}
#musicPages .content p {
	margin-top: .2rem;
	font: .28rem/.45rem '微软雅黑';
}
#musicPages .content pre {
	font: .28rem/.6rem arial,'微软雅黑';
}
#musicPages  h3 {
 font-size: .35rem;
 line-height: .6rem;
}
#musicPages .vux-tab-item {
 font-size: .3rem;
}
#musicPages .vux-tab-selected {
 font-size: .34rem;
 font-weight: bold;
}



