
#comment {
	position: relative;
	height: 50rem;
}
#comment h3 {
	text-indent: .3rem;
	line-height: .8rem;
	border-bottom: 3px solid #ccc;
}
#comment .commentList {
	background-color: #eee;
}
#comment img {
	position: absolute;
	left: 0;
	top: 0;
	width: .8rem;
	height: .8rem;
	border-radius: 50%;
}
#comment li {
	padding: .1rem .3rem;
	margin-bottom: .2rem;
	background-color: #fff;
}
#comment .user {
	position: relative;
}
#comment .username {
	margin-left: 1rem;
	font: .25rem/.4rem '微软雅黑';
}
#comment .username div:nth-of-type(1) {
	color: #233ae7;
}
#comment .mark {
	position: absolute;
	right: .6rem;
	top: .5rem;
}
#comment .mark i {
	font-size: .35rem;
}
#comment .mark span {
	position: absolute;
	top: -.2rem;
	left: .5rem;
}
#comment .content {
	margin-top: .2rem;
	font: .26rem/.5rem '微软雅黑';
}



