
#poster .picList {
	margin: .2rem;
	padding: .2rem;
}
#poster .img {
  	height: 3.72rem;
  	background-size: cover;
}
#poster .author {
  	margin-top: .1rem;
	font: .2rem/.5rem '微软雅黑';
	text-align: center;
}
#poster .content {
  	margin-top: .2rem;
  	font: .25rem/.5rem '微软雅黑';
}
#poster .time {
  	font: .25rem/.5rem '微软雅黑';
  	text-align: right;
}
#poster .mask {
  	position: fixed;
    left: 50%;
    top: 30%;
    width: 0;
    height: 0;
    overflow: hidden;
    color: #fff;
    font: 0.24rem/0.94rem '微软雅黑';
    background: rgba(0,0,0,.9);
    -webkit-transition: .5s;
    transition: .5s;
    z-index: 100;
}
#poster .maskActive {
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}
.mask p {
    margin: .5rem .4rem;
    text-align: left;
    font: .24rem/.24rem '微软雅黑';
}
.mask span {
  	margin-left: .4rem;
  	font: .24rem/.24rem '微软雅黑';
}
.mask img {
    width: 100%;
}



