
#reading img {
		width: 100%;
}
#readList {
    padding: .4rem;
}
#readList li {
    margin-bottom: .5rem;
}
#readList .title {
    position: relative;
}
#readList h3 {
    margin: 0 1.2rem 0 0;
    font: bold .3rem/.5rem '微软雅黑';
}
#readList .title span {
    position: absolute;
    top: 0;
    right: .1rem;
    padding: 0 .3rem;
    border: 1px solid #4c93d4;
    border-radius: .2rem;
    font: .2rem/.5rem '微软雅黑';
}
#readList .author {
    color: #808080;
    font: .26rem/.6rem '微软雅黑';
    height: .6rem;
}
#readList p {
    color: #808080;
    font: .26rem/.4rem '微软雅黑';
}
#readList .special {
    margin-top: .3rem;
}



